import { Component } from '@angular/core';
import { SearchProvider } from '../../services/search.provider';
import { MatDialog } from '@angular/material';
import { DialogComponent } from '../../components/dialog/dialog.component';
import { SpinnerComponent } from '../../components/spinner/spinner.component';

@Component({
    selector: 'search',
    templateUrl: './search.component.html',
    styleUrls: ['./search.component.scss']
})

export class SearchComponent {
    searchStr: String;
    displayData = {
        "city": String,
        "first_name": String,
        "last_name": String,
        "zip": String,
        "title": String,
        "country": String,
        "phone_2_type": String,
        "state": String,
        "phone_1_type": String,
        "street_1": String,
        "street_2": String,
        "phone_2": String,
        "email": String,
        "phone_1": String
    }
    constructor(private searchProvider: SearchProvider, public dialog: MatDialog) {
        this.initiateSearchString();
        this.initiateDisplayData();
    }
    initiateSearchString() {
        this.searchStr = ''
    }
    initiateDisplayData(){
        this.displayData=null;
    }
    search() {
        const spinner = this.dialog.open(SpinnerComponent, { width: '100px' });
        const postData = {
            'unique': this.searchStr
        }
        this.searchProvider.search(postData).then(
            (data: any) => {
                this.displayData=data.data;
                spinner.close();
            },
            (error) => {
                spinner.close();
                const errorText = {
                    text: error
                }
                this.dialog.open(DialogComponent, { width: '500px', data: errorText });
            }
        )
    }
}