import { Component, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { AuthProvider } from '../../services/auth.provider';
import { MatDialog } from '@angular/material';
import { DialogComponent } from '../../components/dialog/dialog.component';
import { SpinnerComponent } from '../../components/spinner/spinner.component';

@Component({
  selector: 'login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class loginComponent {
  authType: string;
  reg: any = {
    uname: String,
    pass: String
  };
  log: any = {
    uname: String,
    pass: String
  };
  @ViewChild('regForm') regForm: ElementRef;

  constructor(private router: Router, private authProvider: AuthProvider, public dialog: MatDialog) {
    this.authType = "Login";
    this.reg = {
      uname: '',
      pass: ''
    };
    this.log = {
      uname: '',
      pass: ''
    };
  }
  goToSignup() {
    this.authType = "Sign Up"
  }
  goToLogin() {
    this.authType = "Login";
  }
  authUser(event) {
    event.preventDefault();
    const spinner = this.dialog.open(SpinnerComponent, { width: '100px' });
    if (this.authType == "Login") {
      const data = {
        username: this.log.uname,
        password: this.log.pass
      }
      this.authProvider.login(data).then(
        (data) => {
          spinner.close();
          this.router.navigateByUrl('/calculator');
        },
        (error) => {
          spinner.close();
          if (error.status == 0) {
            const errorText = {
              text: 'Something went wrong! Please check your network connectivity and try again'
            }
            this.dialog.open(DialogComponent, { width: '500px', data: errorText });
          }
          else if (error.error.errors.error) {
            const errorText = {
              text: error.error.errors.error[0]
            }
            this.dialog.open(DialogComponent, { width: '500px', data: errorText });
          }
          else {
            const errorText = {
              text: "Something went wrong!"
            }
            this.dialog.open(DialogComponent, { width: '500px', data: errorText });
          }
        }
      );
    }
    else if (this.authType == "Sign Up") {
      const data = {
        user: {
          username: this.reg.uname,
          password: this.reg.pass
        }

      }
      this.authProvider.register(data).then(
        (data) => {
          spinner.close();
          this.router.navigateByUrl('/calculator');
        },
        (error) => {
          spinner.close();
          const errorText = {
            text: error
          }
          this.dialog.open(DialogComponent, { width: '500px', data: errorText });
        }
      );
    }
  }
}