import { Component } from '@angular/core';
import { CalculateProvider } from '../../services/calculate.provider';
import { MatDialog } from '@angular/material';
import { DialogComponent } from '../../components/dialog/dialog.component';
import { SpinnerComponent } from '../../components/spinner/spinner.component';

@Component({
    selector: 'calculator',
    templateUrl: './calculator.component.html',
    styleUrls: ['./calculator.component.scss']
})

export class CalculatorComponent {
    calc: any = {
        sprice: Number,
        rpercent: Number,
        nsp: Number,
        dP: Number,
        laf: Number,
        dtf: Number,
        mFactor: Number,
        lMonths: Number,
        tRate: Number,
        regFee: Number
    }
    Address: any = {
        street1: String,
        street2: String,
        city: String,
        state: String,
        zip: String,
        country: String
    }
    Phone: any = {
        number: Number,
        type: String,
    }
    customer: any = {
        title: String,
        firstName: String,
        lastName: String,
        address: this.Address,
        phone1: this.Phone,
        phone2: this.Phone,
        email: String
    }
    leaseResult: any = {
        "base_pay": Number,
        "tot_lease_without_tax": Number,
        "dep_amt": Number, "tot_fee": Number,
        "adj_cap_cost": Number,
        "tot_cost_of_lease": Number,
        "residual_value": Number,
        "drive_off": Number,
        "tot_lease_with_tax": Number,
        "month_rent": Number
    }
    title: String;
    custNum: Number;
    constructor(private calculateProvider: CalculateProvider, public dialog: MatDialog) {
        this.initialiseCalc();
        this.initialilseCustomer();
        this.initiateLeaseResult();
        this.title = "Calculator";
    }
    initialiseCalc() {
        this.calc = {
            sprice: 30545,
            rpercent: 63,
            nsp: 27800,
            dp: 0,
            laf: 595,
            dtf: 90,
            mFactor: 0.002,
            lMonths: 36,
            tRate: 7.75,
            regFee: 400
        }
    }
    initiateLeaseResult() {
        this.leaseResult = {
            "base_pay": null,
            "tot_lease_without_tax": null,
            "dep_amt": null, "tot_fee": null,
            "adj_cap_cost": null,
            "tot_cost_of_lease": null,
            "residual_value": null,
            "drive_off": null,
            "tot_lease_with_tax": null,
            "month_rent": null
        }
    }
    initialilseCustomer() {
        this.customer = {
            title: null,
            firstName: null,
            lastName: null,
            address: {
                street1: "",
                street2: "",
                city: "",
                state: "",
                zip: "",
                country: ""
            },
            phone1: {
                number: null,
                type: null,
            },
            phone2: {
                number: "",
                type: "",
            },
            email: null
        }
    }
    calculate() {
        const spinner = this.dialog.open(SpinnerComponent, { width: '100px' });
        const postData = {
            "sticker_price": this.calc.sprice,
            "residual_percent": this.calc.rpercent,
            "neg_sales_price": this.calc.nsp,
            "down_payment": this.calc.dp,
            "lease_acq_fee": this.calc.laf,
            "doc_tire_fee": this.calc.dtf,
            "money_factor": this.calc.mFactor,
            "lease_months": this.calc.lMonths,
            "tax_rate": this.calc.tRate,
            "reg_fee": this.calc.regFee
        }
        this.calculateProvider.calculateLease(postData).then(
            (data: any) => {
                this.leaseResult = data.data.output_values;
                spinner.close();
                this.title = "Loan Value";
                this.scrollToTop();
            },
            (error) => {
                spinner.close();
                const errorText = {
                    text: error
                }
                this.dialog.open(DialogComponent, { width: '500px', data: errorText });
            }
        )
    }
    addCustomer() {
        this.title = "Customer Details";
        this.scrollToTop();
    }
    saveCustomer() {
        const spinner = this.dialog.open(SpinnerComponent, { width: '100px' });
        const postData = {
            "lease":
            {
                "lease_acq_fee": this.calc.laf,
                "down_payment": this.calc.dp,
                "lease_months": this.calc.lMonths,
                "sticker_price": this.calc.sprice,
                "residual_percent": this.calc.rpercent,
                "doc_tire_fee": this.calc.dtf,
                "money_factor": this.calc.mFactor,
                "tax_rate": this.calc.tRate,
                "neg_sales_price": this.calc.nsp,
                "reg_fee": this.calc.regFee,
                "tot_lease_with_tax": this.leaseResult.tot_lease_with_tax
            },
            "customer": {
                "city": this.customer.address.city,
                "first_name": this.customer.firstName,
                "last_name": this.customer.lastName,
                "zip": this.customer.address.zip,
                "title": this.customer.title,
                "country": this.customer.address.country,
                "phone_2_type": this.getPhoneType(this.customer.phone2.type),
                "state": this.customer.address.state,
                "phone_1_type": this.getPhoneType(this.customer.phone1.type),
                "street_1": this.customer.address.street1,
                "street_2": this.customer.address.street2,
                "phone_2": this.customer.phone2.number,
                "email": this.customer.email,
                "phone_1": this.customer.phone1.number
            }
        }
        this.calculateProvider.saveCustomer(postData).then(
            (data: any) => {
                this.custNum = data.data;
                spinner.close();
                this.title = "Customer Number";
                this.scrollToTop();
            },
            (error) => {
                spinner.close();
                const errorText = {
                    text: error
                }
                this.dialog.open(DialogComponent, { width: '500px', data: errorText });
            }
        )
    }
    startCalculation() {
        this.initialiseCalc();
        this.initialilseCustomer();
        this.initiateLeaseResult();
        this.title = "Calculator";
        this.scrollToTop();
    }
    scrollToTop() {
        setTimeout(() => {
            window.scrollTo(0, 0);
        }, 100)
    }
    goBack() {
        if (this.title == "Loan Value") {
            this.title = "Calculator";
        }
        if (this.title == "Customer Details") {
            this.title = "Loan Value";
        }
    }
    getPhoneType(type) {
        if (type == 'Mobile') { return 0 }
        else if (type == 'Residence') { return 1 }
        else if (type == 'Office') { return 2 }
        else if(type==''){return ""}
    }
}