import { Injectable } from '@angular/core';
import { HttpProvider } from './http.provider';

@Injectable({
    providedIn: 'root'
})

export class CalculateProvider {
    constructor(private http: HttpProvider) {
    }
    calculateLease(data){
        return new Promise((resolve,reject)=>{
            this.http.post('lease/calc/',data).then(
                (response:any)=>{
                    resolve(response);
                },
                (error)=>{
                    let errMessage=""
                    if(error.status=0)
                    (
                        errMessage="Something went wrong! Please check your network connectivity and try again."
                    )
                    else if(error.error.errors&&error.error.errors.error)
                    {
                        errMessage=error.error.errors.error[0];
                    }
                    else
                    {
                        errMessage="Something went wrong!";
                    }
                    reject(errMessage);
                }
            )
        });
    }
    saveCustomer(data){
        return new Promise((resolve,reject)=>{
            this.http.post('customer/save/',data).then(
                (response:any)=>{
                    resolve(response);
                },
                (error)=>{
                    let errMessage=""
                    if(error.status=0)
                    (
                        errMessage="Something went wrong! Please check your network connectivity and try again."
                    )
                    else if(error.error.errors&&error.error.errors.error)
                    {
                        errMessage=error.error.errors.error[0];
                    }
                    else if(error.error.errors)
                    {
                        let errorKeys=Object.keys(error.error.errors);
                        errMessage=error.error.errors[errorKeys[0]];
                    }
                    else
                    {
                        errMessage="Something went wrong!";
                    }
                    reject(errMessage);
                }
            )
        });
    }
    
}