import {Injectable} from '@angular/core';
import {HttpProvider} from './http.provider';

@Injectable({
    providedIn:'root'
})

export class AuthProvider{
    constructor(private http:HttpProvider){

    }
    login(data){
        return new Promise((resolve,reject)=>{
            this.http.post('user_auth/login/',data).then(
                (response:any)=>{
                    this.http.setToken(response.token);
                    resolve(response);
                },
                (error)=>{
                    reject(error);
                }
            )
        })
    }
    register(data){
        return new Promise((resolve,reject)=>{
            this.http.post('user_auth/user/register/',data).then(
                (response:any)=>{
                    this.http.setToken(response.token);
                    resolve(response);
                },
                (error)=>{
                    let errMessage=""
                    if(error.status=0)
                    (
                        errMessage="Something went wrong! Please check your network connectivity and try again."
                    )
                    else if(error.error.errors.username)
                    {
                        errMessage=error.error.errors.username;
                    }
                    else if(error.error.errors.password)
                    {
                        errMessage=error.error.errors.password;
                    }
                    else if(error.error.errors.error)
                    {
                        errMessage=error.error.errors.error[0];
                    }
                    else
                    {
                        errMessage="Something went wrong!";
                    }
                    reject(errMessage);
                }
            )
        })
    }
}